import React from 'react';

import './about-view.css';

const AboutView = ({ about, gatewayid, isSupport3G, localization, onUpdate }) => {
    const { about: aboutLocal } = localization;
    const updateButton = about.server.update.isAvailableUpdate ? <UpdateButton onUpdate={onUpdate} updateData={about.server.update} localization={aboutLocal}/> : null;

    return (
        <div className="about-view">

            <div className="about-view-group container-fluid">
                <div className="group-title col-md-12">
                    {aboutLocal.device.title}:
                </div>

                <div className="container-fluid">
                    <div className="item-title col-md-3">
                        {aboutLocal.device.gatewayid}
                    </div>
                    <div className="item-value col-md-9">
                        { gatewayid }
                    </div>
                </div>
                <div className="container-fluid">
                    <div className="item-title col-md-3">
                        {localization.$3G.isSupport.title}
                    </div>
                    <div className="item-value col-md-9">
                        { (isSupport3G ? localization.$3G.isSupport.yes : localization.$3G.isSupport.no) }
                    </div>
                </div>
            </div>

            <div className="about-view-group container-fluid">
                <div className="group-title col-md-12">
                    {aboutLocal.os.title}:
                </div>

                <div className="container-fluid">
                    <div className="item-title col-md-3">
                        {aboutLocal.os.platform}
                    </div>
                    <div className="item-value col-md-9">
                        {about.os.platform}
                    </div>
                </div>
                <div className="container-fluid">
                    <div className="item-title col-md-3">
                        {aboutLocal.os.type}
                    </div>
                    <div className="item-value col-md-9">
                        {about.os.type}
                    </div>
                </div>
                <div className="container-fluid">
                    <div className="item-title col-md-3">
                        {aboutLocal.os.arch}
                    </div>
                    <div className="item-value col-md-9">
                        {about.os.arch}
                    </div>
                </div>
                <div className="container-fluid">
                    <div className="item-title col-md-3">
                        {aboutLocal.os.release}
                    </div>
                    <div className="item-value col-md-9">
                        {about.os.release}
                    </div>
                </div>
            </div>

            <div className="about-view-group container-fluid">
                <div className="group-title col-md-12">

                    {aboutLocal.server.title}:
                </div>

                <div className="container-fluid">
                    <div className="item-title col-md-3">
                        {aboutLocal.server.dashboardVersion}
                    </div>
                    <div className="item-value col-md-9">
                        {about.server.version}
                    </div>
                    <div className="item-title col-md-3">
                        {aboutLocal.server.managerVersion}
                    </div>
                    <div className="item-value col-md-9">
                        {about.server.managerVersion}
                    </div>
                </div>
                {updateButton}
            </div>

            <div className="about-view-group container-fluid ender">
                <div className="group-title col-md-12">
                    {localization.vendor.title}:
                </div>

                <div className="container-fluid">
                    <div className="item-title col-md-3">
                        {localization.vendor.name.title}
                    </div>
                    <div className="item-value col-md-9">
                        {localization.vendor.name.value}
                    </div>
                </div>
                <div className="container-fluid">
                    <div className="item-title col-md-3">
                        {localization.vendor.address.title}
                    </div>
                    <div className="item-value col-md-9">
                        {localization.vendor.address.value}
                    </div>
                </div>
                <div className="container-fluid">
                    <div className="item-title col-md-3">
                        {localization.vendor.phone.title}
                    </div>
                    <div className="item-value col-md-9">
                        {localization.vendor.phone.value}
                    </div>
                </div>
            </div>
        </div>
    )
}

const UpdateButton = ({ onUpdate, updateData, localization }) => {
    const { dashboardVersion, managerVersion } = updateData;
    const { dashboardUpdate, managerUpdate } = localization.server;
    const dashboardText = dashboardVersion.isAvailableUpdate ? dashboardUpdate + ': ' + dashboardVersion.message + ' (' + dashboardVersion.date + ')' : null;
    const managerText = managerVersion.isAvailableUpdate ? managerUpdate + ': ' + managerVersion.message + ' (' + managerVersion.date + ')' : null;
    return (
        <div className = "about-view-update-button container-fluid">
            {dashboardText}{' '}{managerText}
			<button className="button" onClick = {onUpdate}>
                <i className="fa fa-refresh" aria-hidden="true"></i>
                {localization.buttons.update}
            </button>
		</div>
    )
}

const AboutButtons = ({ rebootDeviceAction, setFactorySettingsAction, localization }) => {
    const { about } = localization;
    return (
        <div className = "about-view-buttons btn-group col-xs-12">
				<button className="btn btn-default" onClick = {rebootDeviceAction}>{about.buttons.rebootDevice}</button>
                <button className="btn btn-default" onClick = {setFactorySettingsAction}>{about.buttons.setFactorySettings}</button>
		</div>
    )
}

export {
    AboutView,
    AboutButtons
};
