const aboutInfoRequested = () => {
    return {
        type: 'FETCH_ABOUT_DEVICE_REQUEST'
    };
};

const aboutInfoLoaded = (payload) => {
    return {
        type: 'FETCH_ABOUT_DEVICE_SUCCESS',
        payload
    };
};

const fetchAboutInfoFailure = (payload) => {
    return {
        type: 'FETCH_ABOUT_DEVICE_FAILURE',
        payload
    };
};

const showUpdateLoading = () => {
    return {
        type: 'SHOW_UPDATE_LOADING'
    };
};

const hideUpdateLoading = () => {
    return {
        type: 'HIDE_UPDATE_LOADING'
    };
};

const aboutOpenConfirm = (payload) => {
	return {
		type: 'ABOUT_OPEN_CONFIRM',
        payload
	}
};

const dropAllSettingsData = () => {
    return {
        type: 'DROP_ALL_SETTINGS_DATA'
    };
};

const aboutCloseConfirm = () => {
	return {
		type: 'ABOUT_CLOSE_CONFIRM'
	}
};

const fetchAboutInfo = (loginToken, ApiService) => (dispatch) => {
    const head = {
        cmd: 'fetch_about_info',
        loginToken
    }
    dispatch(aboutInfoRequested());

    ApiService.aboutInfoRequest(head).then((response) => {
        if (response.result) {
            dispatch(aboutInfoLoaded(response.data));
        } else {
            console.log(response.msg);
            dispatch(fetchAboutInfoFailure(response.msg));
        }
    }).catch((er) => {
        console.log(er);
        dispatch(fetchAboutInfoFailure('cant_connect_to_server'));
    })
}

const rebootDeviceAction = (loginToken, ApiService) => (dispatch) => {
    const head = {
        cmd: 'reboot_device',
        loginToken
    }

    dispatch(aboutCloseConfirm());

    ApiService.aboutInfoRequest(head).then((response) => {
        if (response.result) {
            alert('Reboot device - command accepted.');
        } else {
            console.log(response.msg);
            // TODO: Make event handling
            dispatch(fetchAboutInfoFailure(response.msg));
        }
    }).catch((er) => {
        console.log(er);
        // TODO: Make event handling
        dispatch(fetchAboutInfoFailure('cant_connect_to_server'));
    })
}

const setFactorySettingsAction = (loginToken, ApiService) => (dispatch) => {
    const head = {
        cmd: 'set_factory_settings',
        loginToken
    }

    dispatch(aboutCloseConfirm());

    ApiService.aboutInfoRequest(head).then((response) => {
        if (response.result) {
            dispatch(dropAllSettingsData());
            alert('Factory settings are set.');
        } else {
            console.log(response.msg);
            // TODO: Make event handling
            dispatch(fetchAboutInfoFailure(response.msg));
        }
    }).catch((er) => {
        console.log(er);
        // TODO: Make event handling
        dispatch(fetchAboutInfoFailure('cant_connect_to_server'));
    })
}

const updateServerAction = (loginToken, ApiService) => (dispatch) => {
    const head = {
        cmd: 'update_server',
        loginToken
    }

    dispatch(aboutCloseConfirm());
    dispatch(showUpdateLoading());

    ApiService.aboutInfoRequest(head).then((response) => {
        if (response.result) {
            dispatch(hideUpdateLoading());
            switch (response.msg) {
                case 'done': {
                    alert('Update the server - command is performed. Reload the page after 10 seconds.');
                    break;
                }
                case 'update_is_not_required': {
                    alert('Update is not required.');
                    break;
                }
                default: {
                    alert('Update the server - unknown response:' + response.msg);
                }
            }
        } else {
            dispatch(hideUpdateLoading());
            console.log(response.msg);
            dispatch(fetchAboutInfoFailure(response.msg));
        }
    }).catch((er) => {
        dispatch(hideUpdateLoading());
        console.log(er);
        dispatch(fetchAboutInfoFailure('cant_connect_to_server'));
    })
}

export {
    fetchAboutInfo,
    rebootDeviceAction,
    setFactorySettingsAction,
    updateServerAction,
    aboutOpenConfirm,
    aboutCloseConfirm
}
