const initialAboutStore = {
    aboutState: 'EMPTY',
    about: {
        client: {
            version: '0.1.1'
        },
        server: {},
        os: {},
        isNeedConfirm: false,
        confirmText: null,
        confirmAction: null
    },
    aboutError: null
}

const aboutReducer = (store = initialAboutStore, action) => {
    const { type, payload } = action;
    switch (type) {
        case 'FETCH_ABOUT_DEVICE_REQUEST': {
            return {
                about: initialAboutStore.about,
                aboutState: 'LOADING'
            }
        }
		case 'FETCH_ABOUT_DEVICE_SUCCESS': {
            return {
                about: {
                    ...initialAboutStore.about,
                    ...payload
                },
                aboutState: 'READY'
            }
        }
		case 'FETCH_ABOUT_DEVICE_FAILURE': {
            return {
                about: initialAboutStore.about,
                aboutState: 'ERROR',
                aboutError: payload
            }
        }
        case 'SHOW_UPDATE_LOADING': {
            return {
                isLoadUpdate: true
            }
        }
        case 'HIDE_UPDATE_LOADING': {
            return {
                isLoadUpdate: false
            }
        }
        case 'ABOUT_OPEN_CONFIRM': {
            const { payload } = action;
            const { confirmText, confirmAction } = payload;
            return {
                about: {
                    ...store.about,
                    isNeedConfirm: true,
                    confirmText,
                    confirmAction
                }
            }
        }
        case 'ABOUT_CLOSE_CONFIRM': {
            return {
                about: {
                    ...store.about,
                    isNeedConfirm: false,
                    confirmText: null,
                    confirmAction: null
                }
            }
        }
        default: return {}
    }
}

export default aboutReducer;
