const authorizationReducer = (store, action) => {
    switch (action.type) {
        case 'LOGIN': {
            const { payload } = action;
            return {
                isLogin: true,
                loginToken: payload.token,
                isSupport3G: payload.isSupport3G
            }
        }
		case 'LOGOUT': {
            return {
                isLogin: false,
                loginToken: null
            }
        }
        default: return {};
    }
}

export default authorizationReducer;
