import React from 'react';
import { connect } from 'react-redux';
import { NavLink } from 'react-router-dom';

import { withLocalization } from '../../hoc';

import ExpertModeToggle from '../../views/expert-mode-toggle';

import './main-menu.css';

const MainMenu = ({
    isSupport3G,
    globalConfIsChanged,
    networkConfIsChanged,
    apn3GConfIsChanged,
    frequencyConfIsChanged,
    isAvailableUpdate,

    localization,
    store
}) => {
    const ico =  <i className="fa fa-pencil-square" aria-hidden="true"></i>;
    const globalConfChangedIco = globalConfIsChanged ? ico : null;
    const networkConfChangedIco = networkConfIsChanged ? ico : null;
    const apn3GConfChangedIco = apn3GConfIsChanged ? ico : null;
    const frequencyConfChangedIco = frequencyConfIsChanged ? ico : null;

    const availableUpdateIco = isAvailableUpdate ? <i className="fa fa-exclamation-circle" aria-hidden="true"></i> : null;

    const conf3G = isSupport3G ? <li><NavLink to="/3g" activeClassName="selected">
        {apn3GConfChangedIco}
        <div className="menu-item">
            {localization.$3G.title}
        </div>
    </NavLink></li> : null;

    return (
        <div className="main-menu container-fluid">
            <ul className="nav">
                <li><NavLink exact to="/" activeClassName="selected">
                    {globalConfChangedIco}
                    <div className="menu-item">
                        {localization.loraGlobalGateway.title}
                    </div>
                </NavLink></li>
                <li><NavLink to="/network" activeClassName="selected">
                    {networkConfChangedIco}
                    <div className="menu-item">
                        {localization.deviceNetwork.title}
                    </div>
                </NavLink></li>
                <li><NavLink to="/frequency" activeClassName="selected">
                    {frequencyConfChangedIco}
                    <div className="menu-item">
                        {localization.loraGlobalFrequency.title}
                    </div>
                </NavLink></li>
                    {conf3G}
                <li><NavLink to="/about" activeClassName="selected">
                    {availableUpdateIco}
                    <div className="menu-item">
                        {localization.about.title}
                    </div>
                </NavLink></li>
            </ul>
            <div className="main-menu-mode-toggle">
                <ExpertModeToggle />
            </div>
        </div>
    )
}

const mapStoreToProps = (store) => {
    // TODO: store uses for show store for developing, remove store
    const { isSupport3G, global_conf, network_conf, conf_3G, frequency_conf, about={} } = store;
	const { isChanged: globalConfIsChanged } = global_conf;
    const { isChanged: networkConfIsChanged } = network_conf;
    const { isChanged: apn3GConfIsChanged } = conf_3G;
    const { isChanged: frequencyConfIsChanged } = frequency_conf;
    const {server = {}} = about;
    const {update = {}} = server;

	return {
        isSupport3G,
		globalConfIsChanged,
        networkConfIsChanged,
        apn3GConfIsChanged,
        frequencyConfIsChanged,
        isAvailableUpdate: update.isAvailableUpdate,

        store
	}
}

export default connect(mapStoreToProps)(withLocalization(MainMenu));
