import ru from './ru';
import en from './en';

const dictionary = (locale) => {
    let currentLocale = null;
    switch (locale) {
        case 'ru': {
            currentLocale = ru;
            break;
        }
        case 'en': {
            currentLocale = en;
            break;
        }
        default: {
            currentLocale = ru;
            break;
        }
    }

    // TODO: made deep merge
    return {
        ...en,
        ...currentLocale
    }
}

export default dictionary;
