import React from 'react';

import TextLabel from '../common/text-label';
import CheckBox from '../common/check-box';

import './network-conf-form.css';

const NetworkConfForm = ({
	data={},
	isDataValid={},
	currentData={},
	onChangeLabel,
	onToggleCheckbox,
	active=true,
	localization,
	onSubmit
}) => {
	const { deviceNetwork } = localization;
	const {
		eth0='',
		address='',
		netmask='',
		gateway
	} = data;

	const {
		eth0: currentEth0 = '',
		address: currentAddress = '',
		netmask: currentNetmask = '',
		gateway: currentGateway = '',
	} = currentData;

	const {
		eth0: isEth0Valid = false,
		address: isAddressUpValid = false,
		netmask: isNetmaskValid = false,
		gateway: isGatewayValid = false
	} = isDataValid;

	let checkBoxActive = active;
	let lablesActive = eth0 !== 'dhcp';
	lablesActive = active ? lablesActive : false;

	return (
		<div className="network-conf-form container-fluid">

			<CheckBox
				title={deviceNetwork.isStatic.title}
				dataDescription={{description: deviceNetwork.isStatic.description}}
				fieldName="eth0"
				fieldValue={eth0 === 'static'}
				currentFieldValue={currentEth0 === 'static'}
				isValid={isEth0Valid}
				notValidDescription={deviceNetwork.isStatic.validRequires}
				onToggleCheckbox={onToggleCheckbox}
				active={checkBoxActive}
			/>

			<TextLabel
				title={deviceNetwork.address.title}
				dataDescription={{
					description: deviceNetwork.address.description,
					requires: deviceNetwork.address.validRequires
				}}
				fieldName="address"
				fieldValue={address}
				currentFieldValue={currentAddress}
				isValid={isAddressUpValid}
				notValidDescription={deviceNetwork.address.validRequires}
				onChangeLabel={onChangeLabel}
				active={lablesActive}
				onSubmit={onSubmit}
			/>

			<TextLabel
				title={deviceNetwork.netmask.title}
				dataDescription={{
					description: deviceNetwork.netmask.description,
					requires: deviceNetwork.netmask.validRequires
				}}
				fieldName="netmask"
				fieldValue={netmask}
				currentFieldValue={currentNetmask}
				isValid={isNetmaskValid}
				notValidDescription={deviceNetwork.netmask.validRequires}
				onChangeLabel={onChangeLabel}
				active={lablesActive}
				onSubmit={onSubmit}
			/>

			<TextLabel
				title={deviceNetwork.gateway.title}
				dataDescription={{
					description: deviceNetwork.gateway.description,
					requires: deviceNetwork.gateway.validRequires
				}}
				fieldName="gateway"
				fieldValue={gateway}
				currentFieldValue={currentGateway}
				isValid={isGatewayValid}
				notValidDescription={deviceNetwork.gateway.validRequires}
				onChangeLabel={onChangeLabel}
				active={lablesActive}
				onSubmit={onSubmit}
			/>

		</div>
	)
}

const NetworkConfFormButtons = ({ dataState, setConfigs, setDefault, onReRequestData, localization }) => {
	const { deviceNetwork } = localization;
	const setConfigsButton = dataState === 'READY' ? <button className="btn btn-success" onClick = {setConfigs}>{deviceNetwork.buttons.save}</button> : null;
	return (
		<div className = "network-conf-form-buttons btn-group">
				{setConfigsButton}
				<button className="btn btn-default" onClick = {onReRequestData}>{deviceNetwork.buttons.reRequest}</button>
		</div>
	)
}

export { NetworkConfForm, NetworkConfFormButtons };
