import React from 'react';
import { connect } from 'react-redux';
import compose from '../../../utils/compose';

import { withApiService, withLocalization } from '../../hoc';

import {
    fetchAboutInfo,
    rebootDeviceAction,
    setFactorySettingsAction,
    updateServerAction,
    aboutOpenConfirm,
    aboutCloseConfirm
} from '../../../actions';

import { AboutView, AboutButtons } from '../../views/about-view';
import ConfirmForm from '../../views/common/confirm-form';
import MainPage from '../../views/common/main-page';
import Spinner from '../../views/common/spinner';
import ErrorIndicator from '../../views/common/error-indicator';

const AboutPage = ({
    loginToken,
    isSupport3G,
    gatewayid = '---',
    aboutState,
    aboutError,
    about,
    isNeedConfirm ,confirmText, confirmAction,

    fetchAboutInfo,
    rebootDeviceAction,
    setFactorySettingsAction,
    updateServerAction,
    aboutOpenConfirm,
    aboutCloseConfirm,

    ApiService,
    localization
}) => {
    if (aboutState === 'EMPTY') {
        fetchAboutInfo(loginToken, ApiService);
    };

    const rebootDeviseConfirmData = {
        confirmText: localization.about.confirmReqests.onReboot,
		confirmAction: () => rebootDeviceAction(loginToken, ApiService)
    };
    const setFactorySettingsConfirmData = {
        confirmText: localization.about.confirmReqests.onSetFactorySettings,
		confirmAction: () => setFactorySettingsAction(loginToken, ApiService)
    };
    const updateServerConfirmData = {
        confirmText: localization.about.confirmReqests.onUpdate,
		confirmAction: () => updateServerAction(loginToken, ApiService)
    };

    let content;
    switch (aboutState) {
        case 'READY': {
            content = <AboutView
                about={about}
                gatewayid={gatewayid}
                isSupport3G={isSupport3G}
                localization={localization}
                onUpdate={()=>aboutOpenConfirm(updateServerConfirmData)}
            />;
            break;
        }
        case 'LOADING': content = <Spinner />
            break;
        case 'ERROR': content = <ErrorIndicator error={aboutError}/>
            break;
        default: {
            content = null
        }
    }

    let buttons = null;
    if (isNeedConfirm) {
        buttons = <ConfirmForm confirmText={confirmText} onConfirm={confirmAction} onCancel={()=>aboutCloseConfirm()}/>
    } else {
        buttons = <AboutButtons
            rebootDeviceAction={()=>aboutOpenConfirm(rebootDeviseConfirmData)}
            setFactorySettingsAction={()=>aboutOpenConfirm(setFactorySettingsConfirmData)}
            localization={localization}
        />;
    }

    return (
        <MainPage
            name={localization.about.title}
            content={content}
            buttons={buttons}
        />
    )
}

const mapStoreToProps = ({ loginToken, isSupport3G, aboutState, aboutError, about, gatewayid }) => {
    const { isNeedConfirm ,confirmText, confirmAction } = about;
    return {
        loginToken,
        isSupport3G,
        aboutState,
        aboutError,
        gatewayid,
        about,
        isNeedConfirm ,confirmText, confirmAction
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        fetchAboutInfo: (loginToken, ApiService) => fetchAboutInfo(loginToken, ApiService)(dispatch),
        rebootDeviceAction: (loginToken, ApiService) => rebootDeviceAction(loginToken, ApiService)(dispatch),
        setFactorySettingsAction: (loginToken, ApiService) => setFactorySettingsAction(loginToken, ApiService)(dispatch),
        updateServerAction: (loginToken, ApiService) => updateServerAction(loginToken, ApiService)(dispatch),
        aboutOpenConfirm: (payload) => dispatch(aboutOpenConfirm(payload)),
    	aboutCloseConfirm: () => dispatch(aboutCloseConfirm())
    }
}

export default compose(
    connect(mapStoreToProps, mapDispatchToProps),
    withApiService,
    withLocalization
)(AboutPage);
