import React from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';

import MainMenu from '../main-menu';
import GlobalConfPage from '../../containers/global-conf-page';
import NetworkConfPage from '../../containers/network-conf-page';
import FrequencyConfPage from '../../containers/frequency-conf-page';
import FrequencyPresetsPage from '../../containers/frequency-presets-page';
import Conf3GPage from '../../containers/3G-conf-page';
import AboutPage from '../../containers/about-page';

import './protected-content-view.css';

const ProtectedContentView = ({ expertMode }) => {
    const frequencyPage = expertMode ? FrequencyConfPage : FrequencyPresetsPage;
    return (
		<div className="protected-content">
			<Router>
				<div className="menu col-md-2">
					<MainMenu />
				</div>
				<div className="content col-md-10">
					<Switch>
						<Route
							path='/'
							exact
							component={GlobalConfPage}
						/>
                        <Route
							path='/frequency'
							exact
							component={frequencyPage}
						/>
						<Route
							path='/network'
							exact
							component={NetworkConfPage}
						/>
                        <Route
							path='/3g'
							exact
							component={Conf3GPage}
						/>
                        <Route
							path='/about'
							exact
							component={AboutPage}
						/>
					</Switch>
				</div>
			</Router>
		</div>
	)
}

export default ProtectedContentView;
