import mainAppConfigsReducer from './main-app-configs-reducer';
import authorizationReducer from './authorization-reducer';
import globalConfReducer from './global-conf-reducer';
import networkConfReducer from './network-conf-reducer';
import conf3GReducer from './3g-conf-reducer';
import frequencyConfReducer from './frequency-conf-reducer';
import frequencyPresetsReducer from './frequency-presets-reducer';
import aboutReducer from './about-reducer';

const initStore = {
		language: 'ru',
		langFromDeviceState: 'EMPTY',
		isLogin: false,
		loginToken: null,
		isSupport3G: false,
		expertMode: false,
		aboutState: 'EMPTY',
		aboutError: null,
		isLoadUpdate: false,
		about: {
			client: {
				version: '0.1.1'
			},
			server: {},
			os: {},
			isNeedConfirm: false,
			confirmText: null,
			confirmAction: null
		},
		global_conf: {
			dataState: 'EMPTY',
			isChanged: false,
			error: null,
			currentData: {},
			data: {},
			isDataValid: {},
			isNeedConfirm: false,
			confirmText: null,
			confirmAction: null
		},
		network_conf: {
			dataState: 'EMPTY',
			isChanged: false,
			error: null,
			currentData: {},
			data: {},
			isDataValid: {},
			isNeedConfirm: false,
			confirmText: null,
			confirmAction: null
		},
		conf_3G: {
			dataState: 'EMPTY',
			isChanged: false,
			error: null,
			currentData: {},
			data: {},
			isDataValid: {},
			isNeedConfirm: false,
			confirmText: null,
			confirmAction: null
		},
		frequency_conf: {
			dataState: 'EMPTY',
			isChanged: false,
			error: null,
			currentData: {},
			data: {},
			isDataValid: {},
			descriptionOfNotValid: '',
			radios: {},
			currentRadios: {},
			isNeedConfirm: false,
			confirmText: null,
			confirmAction: null
		},
		frequency_presets: {
			selectedItem: 'manual',
			listState: 'EMPTY',
			list: [],
			presetsDataState: {},
			presetsData: {},
			presetsDataErrors: {},
			error: null,
			isNeedConfirm: false,
			confirmText: null,
			confirmAction: null
		}
	};

const reducer = (store = initStore, action) => {
	switch (action.type) {
		case 'SET_LANGUAGE':
		case 'FETCH_DEVICE_LANGUAGE_LOADING':
		case 'FETCH_DEVICE_LANGUAGE_SUCCESS':
		case 'FETCH_DEVICE_LANGUAGE_FAILURE':
		case 'DROP_ALL_SETTINGS_DATA':
		case 'TOGGLE_EXPERT_MODE': {
            return {
				...store,
                ...mainAppConfigsReducer(store, action)
            }
        }

		case 'LOGIN':
		case 'LOGOUT': {
			// TODO: move to separate file
			return {
				...store,
				global_conf: {
					...store.global_conf,
					error: null,
					dataState: store.global_conf.dataState === 'ERROR' ? 'EMPTY' : store.global_conf.dataState
				},
				network_conf: {
					...store.network_conf,
					error: null,
					dataState: store.network_conf.dataState === 'ERROR' ? 'EMPTY' : store.network_conf.dataState
				},
				conf_3G: {
					...store.conf_3G,
					error: null,
					dataState: store.conf_3G.dataState === 'ERROR' ? 'EMPTY' : store.conf_3G.dataState
				},
				frequency_conf: {
					...store.frequency_conf,
					error: null,
					dataState: store.frequency_conf.dataState === 'ERROR' ? 'EMPTY' : store.frequency_conf.dataState
				},
				frequency_presets: {
					...store.frequency_presets,
					presetsDataState: {},
					error: null,
					listState: 'EMPTY'
				},
				aboutState: 'EMPTY',
				aboutError: null,
				...authorizationReducer(store, action)
			}
		}

		case 'FETCH_GLOBAL_CONFIGS_REQUEST':
		case 'FETCH_GLOBAL_CONFIGS_SUCCESS':
		case 'FETCH_GLOBAL_CONFIGS_FAILURE':
		case 'GLOBAL_CONFIGS_ERROR':
		case 'EDIT_GLOBAL_CONFIGS':
		case 'SAVED_GLOBAL_CONFIGS':
		case 'GLOBAL_OPEN_CONFIRM':
		case 'GLOBAL_CLOSE_CONFIRM': {
			return {
				...store,
				...globalConfReducer(store, action)
			}
		}

		case 'FETCH_NETWORK_CONFIGS_REQUEST':
		case 'FETCH_NETWORK_CONFIGS_SUCCESS':
		case 'FETCH_NETWORK_CONFIGS_FAILURE':
		case 'NETWORK_CONFIGS_ERROR':
		case 'EDIT_NETWORK_CONFIGS':
		case 'SAVED_NETWORK_CONFIGS':
		case 'NETWORK_OPEN_CONFIRM':
		case 'NETWORK_CLOSE_CONFIRM': {
			const { network_conf } = store;
			return {
				...store,
				network_conf: networkConfReducer(network_conf, action)
			}
		}

		case 'FETCH_3G_CONFIGS_REQUEST':
		case 'FETCH_3G_CONFIGS_SUCCESS':
		case 'FETCH_3G_CONFIGS_FAILURE':
		case '3G_CONFIGS_ERROR':
		case 'EDIT_3G_CONFIGS':
		case 'SAVED_3G_CONFIGS':
		case '3G_OPEN_CONFIRM':
		case '3G_CLOSE_CONFIRM': {
			const { conf_3G } = store;
			return {
				...store,
				conf_3G: conf3GReducer(conf_3G, action)
			}
		}

		case 'CLEAR_FREQUENCY_CONFIGS':
		case 'FETCH_FREQUENCY_CONFIGS_REQUEST':
		case 'FETCH_FREQUENCY_CONFIGS_SUCCESS':
		case 'FETCH_FREQUENCY_CONFIGS_FAILURE':
		case 'FREQUENCY_CONFIGS_ERROR':
		case 'SET_PRESET_TO_FREQUENCY_CONFIG':
		case 'EDIT_FREQUENCY_CONFIGS':
		case 'FREQUENCY_PRESETS_FILE_LOADED':
		case 'FREQUENCY_OPEN_CONFIRM':
		case 'FREQUENCY_CLOSE_CONFIRM': {
			return {
				...store,
				...frequencyConfReducer(store, action)
			}
		}

		case 'FETCH_FREQUENCY_PRESETS_LIST_REQUEST':
		case 'FETCH_FREQUENCY_PRESETS_LIST_SUCCESS':
		case 'FETCH_FREQUENCY_PRESETS_LIST_FAILURE':
		case 'FETCH_FREQUENCY_PRESET_REQUEST':
		case 'FETCH_FREQUENCY_PRESET_SUCCESS':
		case 'FETCH_FREQUENCY_PRESET_FAILURE':
		case 'FREQUENCY_PRESETS_ERROR':
		case 'FREQUENCY_PRESETS_SELECT_ITEM':
		case 'FREQUENCY_PRESETS_OPEN_CONFIRM':
		case 'FREQUENCY_PRESETS_CLOSE_CONFIRM': {
			return {
				...store,
				frequency_presets: frequencyPresetsReducer(store, action)
			}
		}

		case 'FETCH_ABOUT_DEVICE_REQUEST':
		case 'FETCH_ABOUT_DEVICE_SUCCESS':
		case 'FETCH_ABOUT_DEVICE_FAILURE':
		case 'SHOW_UPDATE_LOADING':
        case 'HIDE_UPDATE_LOADING':
		case 'ABOUT_OPEN_CONFIRM':
		case 'ABOUT_CLOSE_CONFIRM': {
			return {
				...store,
				...aboutReducer(store, action)
			}
		}

		default: return store;
	}
}

export default reducer;
